.loader {
    border: 16px solid white; /* Серый цвет #f3f3f3*/
    border-top: 16px solid #3498db; /* Синий цвет */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
    margin: 0 auto;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}